.container {
    margin: 100px auto 0;
    display: flex;
    flex-direction: column;
    max-width: 584px;
    width: 100%;
}

.burger {
    display: flex;
    flex-direction: column;
    gap: 16px;
    justify-items: end;
    min-height: 400px;
}

.burger_hover {
    box-shadow: inset 0px -2px 0px #3C39EC;
}

.bun {
    margin: 0 0 0 32px;
}

.article {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 568px;
}

.ingridients {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-height: 50vh;
    overflow-y: scroll;
    width: 100%;
}

.ingridients::-webkit-scrollbar {
    width: 8px;
    padding-left: 8px;
}

.ingridients::-webkit-scrollbar-track {
    background: #2F2F37;
}

.ingridients::-webkit-scrollbar-thumb {
    background: #8585AD;
}

.total {
    margin: 40px 16px 52px auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
}

.loading {
    animation: loading 1s infinite;
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
}

@keyframes loading {
    from {
      width: 0px;
    }
    to {
      width: 25px;
    }
  }